/*
Reset.css
Updated: 2021-08-25
Author: Youni
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
button, input, select, textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}

button, input {
  line-height: normal;
}

:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul, li {
  list-style: none;
}

li {
  display: list-item;
}

table {
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
}

tr {
  display: table-row;
}

thead {
  display: table-header-group;
}

tbody {
  display: table-row-group;
}

tfoot {
  display: table-footer-group;
}

col {
  display: table-column;
}

colgroup {
  display: table-column-group;
}

td,
th {
  display: table-cell;
}

caption {
  display: table-caption;
}

th {
  font-weight: bolder;
  text-align: center;
}

caption {
  text-align: center;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

a {
  text-decoration: none;
}

a:focus,
a:active,
a:hover {
  outline: 0;
}

h1, h2, h3, h4, h5, h6,
b, strong {
  font-weight: bolder;
}

img {
  margin: 0;
  padding: 0;
  border: 0;
}

/* Begin bidirectionality settings (do not change) */
BDO[DIR=ltr] {
  direction: ltr;
  unicode-bidi: bidi-override;
}

BDO[DIR=rtl] {
  direction: rtl;
  unicode-bidi: bidi-override;
}

*[DIR=ltr] {
  direction: ltr;
  unicode-bidi: embed;
}

*[DIR=rtl] {
  direction: rtl;
  unicode-bidi: embed;
}

@media print {
  h1 {
    page-break-before: always;
  }

  h1, h2, h3, h4, h5, h6 {
    page-break-after: avoid;
  }

  ul, ol, dl {
    page-break-before: avoid;
  }
}
::-webkit-scrollbar {
  width: 12px;
  height: 8px;
  border: 3px solid #ffffff;
}

::-webkit-scrollbar-button:start:decrement,
::-webkit-scrollbar-button:end:increment {
  display: block;
  height: 10px;
  background: #ffffff;
}

::-webkit-scrollbar-track {
  background: #ffffff;
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  height: 50px;
  width: 50px;
  background: #ff8955;
  -webkit-border-radius: 8px;
  border-radius: 8px;
}

.v-application {
  font-family: "Maplestory", sans-serif !important;
}

@font-face {
  font-family: "Maplestory";
  font-weight: normal;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Maplestory";
  font-weight: bold;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.ttf") format("truetype");
  font-display: swap;
}
div {
  font-family: "Maplestory";
  color: #5e5e5e;
}

a {
  color: #5e5e5e;
}

a:link {
  color: #5e5e5e;
}

a:visited {
  color: #5e5e5e;
}

a:active {
  color: #5e5e5e;
}

.send {
  display: block;
  color: #fff;
  margin: auto;
  padding: 20px;
  background: #cf5724;
  border: 1px solid #cf5724;
  font-size: 2em;
  position: fixed;
  z-index: 80;
  bottom: 0;
  width: 100%;
}
.send button {
  width: 100%;
  text-align: center;
  height: 100%;
  border: 0;
  background: 0;
  color: #fff;
}

.send:hover {
  background: #ff8955;
}

.rating {
  padding-top: 100px;
}
.rating .star-rating {
  display: flex;
  flex-direction: row-reverse;
  font-size: 2em;
  justify-content: space-around;
  padding: 0 0.2em;
  text-align: center;
  width: 5em;
  margin: 10px auto;
}
.rating .star-rating label {
  color: #ccc;
  cursor: pointer;
}
.rating .star-rating input {
  display: none;
}
.rating .star-rating :checked ~ label {
  color: #ffe347;
}

.room-deal-information-container {
  margin-top: 20px;
  color: #222222;
  border: 1px solid #dddddd;
}

.room-deal-information-content-wrapper {
  min-height: 50px;
  display: flex;
}

.room-deal-informtaion-content-title {
  font-size: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  background-color: #f9f9f9;
}

.room-deal-information-content {
  width: 100%;
  font-size: 14px;
}

.room-deal-option-selector {
  display: flex;
  align-items: center;
  padding: 15px;
}

.room-deal-option-item {
  width: 100px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #cccccc;
  border-radius: 5px;
  cursor: pointer;
}

.room-deal-option-item:last-child {
  margin-left: 10px;
}

.room-deal-option-notice {
  margin-left: auto;
  font-size: 14px;
  color: #888888;
}

.room-deal-option-item-deposit {
  margin-left: 10px;
}

.room-deal-information-wrapper {
  display: flex;
  flex-direction: column;
}

.room-deal-information-item-wrapper {
  display: flex;
  align-items: center;
  margin-left: 10px;
  height: 46px;
  width: 100%;
}
.room-deal-information-item-wrapper > input {
  border: 1px solid #dddddd;
  width: 140px;
  height: 100%;
  padding: 0 15px;
  font-size: 15px;
}

.room-deal-inforamtion-won {
  margin: 0 10px;
}

.room-deal-information-example {
  color: #888888;
}

.room-deal-inforamtion-divide {
  font-size: 22px;
  margin: 0 8px;
  color: #222222;
  font-weight: 100;
}

.room-deal-close-button-wrapper {
  margin-left: auto;
  cursor: pointer;
}

.room-deal-close-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 22px;
  background-color: #666666;
  color: #dcdcdc;
}

.room-deal-cliked {
  background-color: #ebebeb;
  color: #aaaaaa;
}

.room-file-upload-example {
  height: 100%;
}

.file-preview-content-container {
  height: 100%;
}

.room-file-upload-wrapper {
  margin: 20px;
  border: 1px solid #dddddd;
  background-color: #f4f4f4;
  min-height: 150px;
  font-size: 15px;
  color: #888888;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.room-file-upload-example-container {
  display: flex;
  align-items: center;
  justify-content: center;
  /* height: 100%;
  width: 100%; */
}

.image-box {
  margin-top: 30px;
  padding-bottom: 20px;
  text-align: center;
}
.image-box input[type=file] {
  position: absolute;
  width: 0;
  height: 0;
  padding: 0;
  overflow: hidden;
  border: 0;
}
.image-box label {
  display: inline-block;
  padding: 10px 20px;
  background-color: #ff8955;
  color: #fff;
  vertical-align: middle;
  font-size: 15px;
  cursor: pointer;
  border-radius: 5px;
}

.file-preview-wrapper {
  padding: 10px;
  position: relative;
}
.file-preview-wrapper > img {
  position: relative;
  width: 190px;
  height: 130px;
  z-index: 10;
}
.file-preview-wrapper .file-close-button {
  position: absolute;
  /* align-items: center; */
  line-height: 18px;
  z-index: 40;
  font-size: 18px;
  right: 5px;
  top: 10px;
  color: #fff;
  font-weight: bold;
  background-color: #666666;
  width: 20px;
  height: 20px;
  text-align: center;
  cursor: pointer;
}

.file-preview-container {
  height: 100%;
  display: flex;
  flex-wrap: wrap;
}

@media all and (max-width: 767px) {
  .text textarea {
    width: 70%;
    min-width: 300px;
    border: 1px solid #dddddd;
    height: 100px;
    resize: none;
    padding: 5px;
    margin-bottom: 110px;
  }

  .main-container {
    width: 70%;
    min-width: 300px;
    height: auto;
    margin: 0 auto;
  }

  .file-preview-wrapper > img {
    width: 100%;
    height: auto;
  }

  .file-preview-wrapper-upload {
    margin: 10px;
    padding-top: 16px;
    width: 100%;
    height: auto;
  }
}
@media all and (min-width: 768px) {
  .star-rating label:hover,
.star-rating label:hover ~ label {
    color: #ff8955;
  }

  .text textarea {
    width: 45%;
    min-width: 700px;
    border: 1px solid #dddddd;
    height: 100px;
    resize: none;
    padding: 5px;
    margin-bottom: 110px;
  }

  .main-container {
    width: 45%;
    min-width: 700px;
    height: auto;
    margin: 0 auto;
  }

  .file-preview-wrapper-upload {
    margin: 10px;
    padding-top: 16px;
    width: 190px;
    height: 130px;
  }
}